import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Redirect } from "react-router-dom";

import PettyCashTypeUpdateForm from "./updateForm";

import notification from "../../../../components/notification";
import { Spin } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/cashBank/pettyCashType/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/setup/petty-cash-types";

class UpdatePettyCashType extends Component {
  componentDidUpdate() {
    // Display error notif
    if (this.props.pettyCashType.error) {
      notification("error", this.props.pettyCashType.error);
      this.props.clearError();
    }

    if (this.props.pettyCashType.success) {
      this.props.history.replace(redirectTo);
    }
  }
  // Get All data diawal
  componentDidMount() {
    const { editPettyCashType } = this.props;
    const { id } = this.props.match.params;
    editPettyCashType(id);
  }

  handleSubmit = value => {
    const { updatePettyCashType } = this.props;
    const { id } = this.props.match.params;
    updatePettyCashType(id, value);
  };

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="pettyCashTypes.update.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          {this.props.pettyCashType.loadingEdit ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
              <PettyCashTypeUpdateForm
                auth={this.props.auth}
                submit={this.handleSubmit}
                pettyCashType={this.props.pettyCashType}
              />
            )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { auth: state.auth, pettyCashType: state.pettyCashType };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(UpdatePettyCashType);
